:root {
    --background: #1e1b4b;
    --foreground: #f1f5f9;
}

/* Light theme variables */
.light {
    --background: #f8fafc;
    --foreground: #312e81;
}

.light body {
    color: var(--foreground);
    background: linear-gradient(135deg, #f8fafc 0%, #e2e8f0 100%);
}

* {
    box-sizing: border-box;
    padding: 0;
    margin: 0;
}

body {
    color: var(--foreground);
    background: linear-gradient(135deg, #0f0f23 0%, #1a1a2e 100%);
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Inter', sans-serif;
    min-height: 100vh;
    overflow-x: hidden;
}

/* Фон контейнера приложения */
.app-container {
    background: linear-gradient(135deg, #0f0f23 0%, #1a1a2e 100%);
}

/* Дополнительные фоновые эффекты */
.background-effects {
    position: fixed;
    inset: 0;
    pointer-events: none;
}

.bg-effect-1 {
    position: absolute;
    top: 0;
    left: 25%;
    width: 24rem;
    height: 24rem;
    background: rgba(99, 102, 241, 0.1);
    border-radius: 50%;
    filter: blur(3rem);
}

.bg-effect-2 {
    position: absolute;
    bottom: 0;
    right: 25%;
    width: 24rem;
    height: 24rem;
    background: rgba(147, 51, 234, 0.1);
    border-radius: 50%;
    filter: blur(3rem);
}

/* Основной стеклянный эффект */
.glass {
    background: rgba(255, 255, 255, 0.05);
    backdrop-filter: blur(20px);
    border: 1px solid rgba(255, 255, 255, 0.1);
    border-radius: 16px;
}

/* Темный стеклянный эффект */
.glass-dark {
    background: rgba(17, 24, 39, 0.95);
    backdrop-filter: blur(20px);
    border: 1px solid rgba(255, 255, 255, 0.1);
    border-radius: 16px;
    box-shadow: 0 8px 32px rgba(0, 0, 0, 0.3);
}

/* Стеклянные карточки с hover эффектом */
.glass-card {
    background: rgba(255, 255, 255, 0.05);
    backdrop-filter: blur(20px);
    border: 1px solid rgba(255, 255, 255, 0.1);
    border-radius: 12px;
    padding: 1rem;
    transition: all 0.3s ease;
}

.glass-card:hover {
    background: rgba(255, 255, 255, 0.1);
    border-color: rgba(255, 255, 255, 0.2);
    box-shadow: 0 8px 32px rgba(0, 0, 0, 0.15);
    transform: translateY(-2px);
}

/* Custom scrollbar */
::-webkit-scrollbar {
    width: 6px;
    height: 6px;
}

::-webkit-scrollbar-track {
    background: rgba(99, 102, 241, 0.1);
    border-radius: 3px;
}

::-webkit-scrollbar-thumb {
    background: rgba(99, 102, 241, 0.4);
    border-radius: 3px;
}

::-webkit-scrollbar-thumb:hover {
    background: rgba(99, 102, 241, 0.6);
}

/* Mobile scrolling improvements */
@media (max-width: 1024px) {
    ::-webkit-scrollbar {
        width: 4px;
        height: 4px;
    }
}

/* Smooth scrolling for touch devices */
* {
    -webkit-overflow-scrolling: touch;
}

/* Prevent horizontal scroll on mobile */
html, body {
    overflow-x: hidden;
    width: 100%;
}

/* Mobile-specific utilities */
@media (max-width: 768px) {
    .mobile-scroll {
        overflow-x: auto;
        -webkit-overflow-scrolling: touch;
        scrollbar-width: thin;
    }
}

::-webkit-scrollbar-thumb:hover {
    background: rgba(255, 255, 255, 0.3);
}

/* Optimized drag and drop styles */
.dragging {
    opacity: 0.5;
    transform: rotate(2deg) scale(1.02);
    transition: none;
    will-change: transform, opacity;
    pointer-events: none;
}

.drop-zone {
    background: rgba(14, 165, 233, 0.1);
    border: 2px dashed rgba(14, 165, 233, 0.3);
    transition: all 0.2s ease;
}

.drop-zone-active {
    background: rgba(14, 165, 233, 0.2);
    border-color: rgba(14, 165, 233, 0.5);
    transform: scale(1.01);
}

/* Performance optimizations for drag and drop */
.dnd-container {
    will-change: transform;
    transform: translateZ(0);
}

.task-card {
    will-change: transform, opacity;
    backface-visibility: hidden;
    transform: translateZ(0);
}

.task-card.dragging {
    z-index: 1000;
    box-shadow: 0 25px 50px -12px rgba(0, 0, 0, 0.5);
}

.drag-overlay {
    pointer-events: none;
    z-index: 999;
}

/* Smooth transitions for non-dragging elements */
.task-card:not(.dragging) {
    transition: transform 0.2s ease, opacity 0.2s ease;
}

/* Column drop indicators */
.column-drop-indicator {
    height: 2px;
    background: linear-gradient(90deg, transparent, #6366f1, transparent);
    border-radius: 1px;
    opacity: 0;
    transition: opacity 0.2s ease;
}

.column-drop-indicator.active {
    opacity: 1;
    animation: pulse 1s infinite;
}

/* Light theme styles */
.light-theme {
    background: linear-gradient(135deg, #f1f5f9 0%, #e2e8f0 100%) !important;
}

